.background-image-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay-content {
  position: relative;
  z-index: 1;
  text-align: center;
  color: white;
  font-size: 2rem;
  padding: 20px;
  border-radius: 10px;
}

